<template>
    <div>
        <van-nav-bar class="hader" title="登录" left-text="返回" left-arrow @click-left="back()">
            <a href="http://guanfan.oss-cn-hangzhou.aliyuncs.com/apk/guanfan_v1.0.6.apk" slot="right"
               v-if="isiOS != true && isWeixin != true">
                <van-button type="primary" size="small">安装APP</van-button>
            </a>
            <a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS == true && isWeixin != true">
                <van-button type="primary" size="small">安装APP</van-button>
            </a>
            <a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect"
               slot="right" v-if="isWeixin == true">
                <van-button type="primary" size="small">关注</van-button>
            </a>
        </van-nav-bar>
        <div class="text-center title">
            <div class="text-black text-bold text-25">欢迎饭友回家</div>
            <div class="grayColor text-16 mt25">闲来无事，就想做菜给你吃</div>
        </div>
        <van-cell-group class="padding-lr" :model="ruleForm">
            <van-field
                    v-model="ruleForm.phone"
                    clearable
                    left-icon="phone-o"
                    placeholder="请输入手机号"
                    @click-right-icon="$toast('question')"
            />

            <van-field
                    v-model="ruleForm.verify"
                    center
                    clearable
					left-icon="chat-o"
                    placeholder="请输入验证码"
            >
                <van-button slot="button" size="small" type="primary" @click="getCode()" class="verBtn">{{btnText}}
                </van-button>
            </van-field>
        </van-cell-group>
        <div class="padding-lr">
			<div class="clearfix tipText text-14 mt15 mb15">
			        <div class="left" @click="topsw">密码登录</div>
			</div>
            <van-button type="info" size="large" @click="bindLogin" class="bg-yellow">登录</van-button>
            <van-button type="info" size="large" plain @click="toreg" class="mt15 bg-yellow">注册</van-button>
        </div>
    </div>
</template>

<script>
    import {sendCode} from '@/api/login'
    import {Toast} from 'vant';

    export default {
        name: "codeLogin",
        data() {
            return {
                ruleForm: {
                    phone: '',
                    verify: '',
                    type: 2
                },
                btnText: '获取验证码',
                countdown: 60,
                isWeixin: '',
                isiOS: '',
            }
        },
        computed: {
            isSubmit() {
                return !!this.phone && !!this.verify
            }
        },
        created() {
            this.getwx();
            this.appSource();
        },
        methods: {
            getwx() {
                var ua = window.navigator.userAgent.toLowerCase();
                if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                    this.isWeixin = true;
                } else {
                    this.isWeixin = false;
                }
            },
            appSource() {
                const u = navigator.userAgent,
                    app = navigator.appVersion;
                if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
                    this.isiOS = true;
                } else {
                    this.isiOS = false;
                }
            },
            getCode() {
                if (this.ruleForm.phone == '') {
                    Toast('请输入手机号');
                    return;
                }
                this.sendsms();
            },
            sendsms() {
                sendCode(this.ruleForm.phone, 1).then(response => {
                    let res = response.data;
                    if (res.success) {
                        this.setTime();//开始倒计时
                    } else {
                        Toast(response.message);
                    }
                }).catch(error => {
                    console.log(error)
                })
            },
            //60s倒计时实现逻辑
            setTime() {
                if (this.countdown == 0) {
                    this.btnText = "获取验证码";
                    this.countdown = 60;//60秒过后button上的文字初始化,计时器初始化;
                    return;
                } else {
                    setTimeout(this.setTime, 1000);//每1000毫秒执行一次
                    this.btnText = "(" + this.countdown + "s)后重新发送";
                    this.countdown--;
                }
            },
            bindLogin() {
                if (this.ruleForm.phone.length < 11) {
                    Toast('请输入手机号');
                    return;
                } else if (this.ruleForm.verify.length < 6) {
                    Toast('请输入验证码');
                    return;
                }
                this.$store.dispatch('LoginByCode', this.ruleForm).then(() => {
                    this.getUser()
                }).catch(() => {

                })
            },
            getUser() {
                this.$store.dispatch('GetUserInfo').then(response => {
                    this.$router.go(-1);//返回上一层
                }).catch(error => {
                })
            },
			topsw(){
				this.$router.replace({path: this.redirect || '/pswLogin'});
			},
			toreg(){
				this.$router.replace({path: this.redirect || '/reg'});
			},
			back(){
			    this.$router.go(-1);//返回上一层
			},
        }
    }
</script>

<style scoped>
    .title {
        padding: 10rem 0 5rem;
    }

    .van-button--small {
        height: 28px;
    }

    .van-button--primary.verBtn {
        color: black;
        border-radius: 0.5rem;
        background-color: #fff;
        border: 1px solid black;
    }

    .van-button--info {
        border: none;
    }

    .van-button--plain.bg-yellow {
        background-color: white;
        border: 1px solid #FFC700;
        border-radius: 0.5rem;
        color: #FFC700
    }
</style>
